<template>
  <div class="block">
    <el-page-header class="goback" @back="goBack"></el-page-header>
    <h1></h1>
    <el-table class="el-show" :data="tableData" border style="width: 100%">
      <el-table-column prop="name" label="" width="180"></el-table-column>
      <el-table-column prop="address" label=""></el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  data() {
    return {
      tableData: [
        {
          name: "",
          address: " 1517 "
        },
        {
          name: "",
          address: " 1517 "
        },
        {
          name: "",
          address: " 1519 "
        },
        {
          name: "",
          address: " 1516 "
        }
      ]
    };
  },
  methods: {
    goBack() {
      this.$router.back(-1);
    }
  }
};
</script>

<style scoped>
.block {
  background-color: #fff;
  box-sizing: border-box;
  border: 1px solid #d7d7d7;
}
.block .el-show {
  padding: 50px;
}
.block .goback {
  margin: 18px;
}
</style>



